body {
  box-sizing: border-box;
  background-color: #f4f4f4;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.login-container {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 400px;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

h2 {
  color: #333;
  margin-bottom: 15px;
}

input[type="email"], input[type="password"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: calc(100% - 20px);
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  font-size: 16px;
}

button:hover {
  background-color: #45a049;
}

